import React, { useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core";
import StatisticsBlock from "../components/StatisticsBlock";

const useStyles = makeStyles({
    characterGrid: {
        backgroundColor: "beige",
        fontFamily: "Martel",
        display: "grid",
        gridTemplateColumns: "repeat(2, 1fr)",
        gridAutoRows: "minmax(100px, auto)",
        height: "calc(100vh - 115px)",
    },
    sectionHeading: {
        fontFamily: "MedievalSharp",
    },
    bannerContainer: {
        position: "relative",
        height: "100px",
        top: "-80px",
    },
    itemContainer: {
        position: "relative",
    },
    close: {
        position: "absolute",
        left: "20px",
        top: "-100px",
        width: "40px",
        filter: "invert(42%) sepia(93%) saturate(1352%) hue-rotate(32deg) brightness(60%) contrast(119%)",
    },
    portrait: {
        backgroundColor: "ivory",
        height: "calc(60vh - 55px)",
        zIndex: 0,
        border: "solid",
        borderWidth: "4px",
        borderColor: "beige",
        borderRadius: "20px",
    },
    portraitImg: {
        position: "absolute",
        left: "20%",
        width: "40%",
        border: "solid",
        borderRadius: "50%",
        borderWidth: "10px",
        borderColor: "lightslategrey",
        margin: "68px",
    },
    portraitItem: {
        position: "absolute",
        width: "70px",
        border: "solid",
        borderRadius: "50%",
        borderWidth: "6px",
        borderColor: "lightslategrey",
    },
    portraitItemHelm: {
        left: "90px",
        top: "35px",
    },
    portraitItemBreastplate: {
        left: "90px",
        top: "135px",
    },
    portraitItemGloves: {
        left: "90px",
        top: "235px",
    },
    portraitItemLegs: {
        left: "90px",
        top: "335px",
    },
    inventory: {
        backgroundColor: "ivory",
        border: "solid",
        borderWidth: "4px",
        borderColor: "beige",
        borderRadius: "20px",
    },
    inventoryGrid: {
        border: "5px solid rgb(111,41,97)",
        display: "grid",
        gridTemplateColumns: "repeat(6, 80px)",
        gridAutoRows: "5em",
        inlineSize: "500px",
        alignItems: "center",
        margin: "0 auto",
    },
    statistics: {
        overflowY: "scroll",
        backgroundColor: "ivory",
        gridRowEnd: "span 2",
        border: "solid",
        borderWidth: "4px",
        borderColor: "beige",
        borderRadius: "20px",
    },
    statisticsBlock: {
        padding: "40px 80px",
    },
});

export function HeroProfile(props: any) {
    const classes = useStyles();

    return (
        <div className={classes.characterGrid}>
            <div className={classes.portrait}>
                <br />
                <div className={classes.bannerContainer}>
                    <h1 className="banner">Balthazar the Unblemished</h1>
                </div>
                <div className={classes.itemContainer}>
                    <a href="/#/">
                        <img src="images/close.svg" className={classes.close} alt="close" />
                    </a>
                    <img src="images/cleric.jpg" className={classes.portraitImg} alt="profile" />
                    <img src="images/helm.svg" className={`${classes.portraitItem} ${classes.portraitItemHelm}`} alt="helm" />
                    <img
                        src="images/breastplate.svg"
                        className={`${classes.portraitItem} ${classes.portraitItemBreastplate}`}
                        alt="breastplate"
                    />
                    <img src="images/leg.svg" className={`${classes.portraitItem} ${classes.portraitItemLegs}`} alt="legs" />
                    <img src="images/glove.svg" className={`${classes.portraitItem} ${classes.portraitItemGloves}`} alt="gloves" />
                </div>
            </div>
            <div className={classes.statistics}>
                <br />
                <br />
                <h1 className={classes.sectionHeading}>Statistics</h1>
                <div className={classes.statisticsBlock}>
                    <StatisticsBlock />
                </div>
                <h1 className={classes.sectionHeading}>Other</h1>
                <div className={classes.statisticsBlock}>
                    <p>Reputation 12</p>
                    <p>Initiative 2</p>
                    <p>Spell Resistance Save 14</p>
                    <p>Witch Taint 12</p>
                </div>
                <h1 className={classes.sectionHeading}>Traits</h1>
                <div className={classes.statisticsBlock}>
                    <p>Blessed: A blessing from a Fae Queen, +2 Luck</p>
                    <p>Lost an Arm: Lost in a fall, -3 Dexterity</p>
                    <p>Lost an Eye: Spear to the head, -4 on Ranged attacks</p>
                </div>
            </div>
            <div className={classes.inventory}>
                <br />
                <h1 className={classes.sectionHeading}>Party Inventory</h1>
                <div className={classes.inventoryGrid}>
                    <div>1</div>
                    <div>2</div>
                    <div>3</div>
                    <div>4</div>
                    <div>5</div>
                    <div>6</div>
                    <div>7</div>
                    <div>8</div>
                    <div>9</div>
                    <div>10</div>
                    <div>11</div>
                    <div>12</div>
                </div>
            </div>
        </div>
    );
}

export default HeroProfile;