import React, { useEffect, useState } from "react";

import { Dialog, makeStyles } from "@material-ui/core";

import { MockEncounterService } from "../services/MockEncounterService";
import { Button, DialogActions, DialogContent } from "@material-ui/core";
import EncounterChoice from "../models/EncounterChoice";

const useStyles = makeStyles({
    dialog: {
        // backgroundImage: "url(images/scroll.png)"
    },
    background: {
        position: "absolute",
        opacity: 0.1,
        right: "20px",
    },
    content: {
        minWidth: "520px",
        minHeight: "460px",
        padding: "40px",
        fontFamily: "Martel",
    },
    actions: {
        padding: "30px",
        //paddingBottom: "100px",
        //paddingRight: "80px",
    },
});

type EncounterDialogProps = {
    onClose: (val: number) => void;
    result: number;
    encounterId: string | undefined;
};

function EncounterDialog(props: EncounterDialogProps) {
    const { onClose, result, encounterId } = props;

    const [encounterService] = useState(new MockEncounterService());
    let [paragraph, setParagraph] = useState("");
    let [title, setTitle] = useState("");
    let [choices, setChoices] = useState([] as EncounterChoice[]);
    let [storyStarted, setStoryStarted] = useState(false);

    const open: boolean = encounterId !== undefined;
    const classes = useStyles();

    const handleClose = () => {
        setStoryStarted(false);
        onClose(result);
    };

    const handleCloseClick = (value: any) => {
        onClose(value);
    };

    const handleChoice = (ev: any) => {
        const choiceId = Number.parseInt(ev.currentTarget.id);

        // Tell the story where to go next
        const encounter = encounterService.chooseChoice(choiceId);

        if (encounter != null) {
            console.log(`Selected ${choiceId} for ${encounter.id}`);

            setParagraph(encounter.paragraphText);
            setTitle(encounter.title);
            setChoices(encounter.choices);
        }

        // TODO: How to finish?
    };

    const handleContinue = (ev: any) => {
        const encounter = encounterService.continue();

        if (encounter != null) {
            setParagraph(encounter.paragraphText);
            setChoices(encounter.choices);
        } else {
            // Finished/
            onClose(0);
        }
    };

    useEffect(() => {
        // Initial load of map data
        const startEncounter = () => {
            if (!encounterId) return;

            //console.log(`Encounter ${encounterId}`);

            setStoryStarted(true);

            const encounter = encounterService.startEncounter(encounterId);
            if (!encounter) {
                console.error(`No encounter data`);
                return;
            }
            console.log(`Started ${encounter.id} encounter`);
            setParagraph(encounter.paragraphText);
            setTitle(encounter.title);
            setChoices(encounter.choices);
        };

        // console.log(`useEffect2 ${encounterId} open=${open} storyStarted=${storyStarted}`);

        if (open && !storyStarted) {
            startEncounter();
        }
    }, [encounterId]);

    const renderChoices = (encChoices: EncounterChoice[]) => {
        if (encChoices.length == 0) {
            return <Button onClick={handleContinue}>Continue</Button>;
        }

        return encChoices.map((c: EncounterChoice) => (
            <Button id={c.index.toString()} key={c.index} onClick={handleChoice}>
                {c.text}
            </Button>
        ));
    };

    return (
        // className={classes.dialog}
        <Dialog className={classes.dialog} onClose={handleClose} aria-labelledby="simple-dialog-title" open={open}>
            {/* <DialogTitle id="simple-dialog-title">Set backup account</DialogTitle> */}
            <DialogContent className={classes.content}>
                <img className={classes.background} src="./images/chest.jpg" />
                <h1>{title}</h1>
                <p>{paragraph}</p>
            </DialogContent>
            <DialogActions className={classes.actions}>{renderChoices(choices)}</DialogActions>
        </Dialog>
    );
}

export default EncounterDialog;
