import './App.scss';
import { HashRouter, Route, Switch } from 'react-router-dom';

import Header from "./components/Header";
import OverheadMap from "./pages/OverheadMap";
import HeroProfile from "./pages/HeroProfile";

function App() {
  return (
      <div className="app">
          <Header />
          <HashRouter>
              <Switch>
                  <Route path="/profile/" component={HeroProfile} />
                  <Route path="/" component={OverheadMap} />
              </Switch>
          </HashRouter>
      </div>
  );
}

export default App;
