import React from "react";
import { withStyles, makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";

const StyledTableCell = withStyles((theme) => ({
    head: {
        fontFamily: "Martel",
        backgroundColor: theme.palette.common.black,
        color: theme.palette.common.white,
    },
    body: {
        fontFamily: "Martel",
        fontSize: 14,
    },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
    root: {
        "&:nth-of-type(odd)": {
            backgroundColor: theme.palette.action.hover,
        },
    },
}))(TableRow);

function createData(name: string, base: number, modifier: number, total: number) {
    return { name, base, modifier, total };
}

const rows = [
    createData("Strength", 12, 1, 12),
    createData("Dexterity", 8, -3, 5),
    createData("Consitution", 11, 0, 11),
    createData("Intelligence", 12, 0, 12),
    createData("Wisdom", 15, 0, 15),
    createData("Charisma", 14, 0, 14),
    createData("Luck", 11, 2, 13),
];

const useStyles = makeStyles({
    table: {
        minWidth: 700,
    },
});

export default function StatisticsBlock() {
    const classes = useStyles();

    return (
        <TableContainer component={Paper}>
            <Table className={classes.table} aria-label="customized table">
                <TableHead>
                    <TableRow>
                        <StyledTableCell>Attribute</StyledTableCell>
                        <StyledTableCell align="right">Base</StyledTableCell>
                        <StyledTableCell align="right">Modifier</StyledTableCell>
                        <StyledTableCell align="right">Total</StyledTableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {rows.map((row) => (
                        <StyledTableRow key={row.name}>
                            <StyledTableCell component="th" scope="row">
                                {row.name}
                            </StyledTableCell>
                            <StyledTableCell align="right">{row.base}</StyledTableCell>
                            <StyledTableCell align="right">{row.modifier}</StyledTableCell>
                            <StyledTableCell align="right">{row.total}</StyledTableCell>
                        </StyledTableRow>
                    ))}
                </TableBody>
            </Table>
        </TableContainer>
    );
}
